.md-modal {
    margin: auto;
    position: fixed;
    top: 100px;
    left: 0;
    right: 0;
    width: 50%;
    max-width: 630px;
    min-width: 320px;
    height:calc(100% - 200px);
    overflow: auto;
    z-index: 100001;
    visibility: hidden;

    backface-visibility: hidden;


    .toolbar{
        margin-bottom:50px;
        display:flex;
        justify-content: space-between;
        button {
            background: transparent;
            border: 2px solid #444;
            padding: 5px 10px;
        
        
        }
        .select-style {
            padding: 0;
            margin: 0;
            border: 2px solid #444;
            width: 100px;
            overflow: hidden;
            background: transparent;
        
        
        }
        
        .select-style select {
            padding: 5px 8px;
            width: 130%;
            height: auto;
            border: none;
            box-shadow: none;
            background-color: transparent;
            background-image: none;
            -webkit-appearance: none;
               -moz-appearance: none;
                    appearance: none;
        }
        
        .select-style select:focus {
            outline: none;
        }
    }

}

.md-show {
    visibility: visible;
}

.md-overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    visibility: hidden;
    top: 0;
    left: 0;
    z-index: 100000;
    opacity: 0;
    background: rgba(#e4f0e3, 0.8);

    transition: all 0.3s;
    background-image:url("../../images/paper.png");
}

.md-show~.md-overlay {
    opacity: 1;
    visibility: visible;
    background-color: #e4f0e3;
    
}

#image-overlay{
    width: 100%;
    height: 100%;
    z-index: 200000;
    background:rgba(255,255,255,1);
    position: fixed;
    top: 0;
    left: 0;
   
    display: none;
    padding:0 50px;
    border: 1px solid black;
    ul{
        
        padding:0;
        display:flex;
        flex-wrap: wrap;
       
        li{
            cursor: pointer;
            padding: 2px;
            flex-basis:33.33%;
            height:33.3vh;
            img{
                width:100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    img.prev{
        position: absolute;
        top:50%;
        transform: translateY(-50%);
        left:6px;
        cursor: pointer;

        &.greyout{
            opacity: 0.4;
            cursor: default;

        }
    }
    img.next{
        position: absolute;
        top:50%;
        transform: translateY(-50%);
        right:6px;
        cursor: pointer;
    }
}

.md-content {

    transform: scale(0.8);
    opacity: 0;

    transition: all 0.3s;
}





.md-show.md-modal .md-content {

    transform: scale(1);
    opacity: 1;
    position: relative;

    
    
}

.vocabulary {
    font-family: 'Kalam';
    font-size: 20px;
    color: blue;
    position: relative;

    >h1{
        cursor: pointer;
    }


    dt{
        cursor: pointer;
        display: flex;
        span:last-child{
            background-image: url(../../images/audio.png);
            background-size: 25px 25px;
            background-repeat: no-repeat;
            background-position: center, center;
            width: 25px;
            height: 25px;
            display: block;
            margin-left:10px;
        }
       
    }
    dd{
        cursor: pointer;
        display: flex;
        
        span:last-child{
            background-image: url(../../images/audio.png);
            background-size: 25px 25px;
            background-repeat: no-repeat;
            background-position: left, center;
            height: 25px;
            display: block;
            margin-left:10px;
            min-width:100px;
        }
    }
    img{
        margin-top:50px;
        margin-bottom:15px;
        width: 400px;
        height: 300px;
        cursor: pointer;
    }



    
}


